.__gallery {
    
  background-image: linear-gradient(
      to right bottom,
      rgba(rgb(14, 39, 94), 0.8),
      rgba(black, 0.8)
      ),
      url(../Assets/back.jpg);
      background-size: cover;
      background-position: top;
      position: relative;
      // clip-path: polygon(0 16%, 100% 0, 100% 100%, 0 100%);
      // margin-top: 10rem;

      display: flex;
      align-items: center;
      justify-content: center;

      .gallery2 {
      // margin-top: 6rem;
      display: grid;
  
      grid-template-columns: repeat(2, 40rem);
      grid-template-rows: 20vh repeat(6, 25rem);
      
      grid-gap: 10rem;
      padding: 8rem;

      // margin-top: 8rem;
      

      &__item {
          &--0{
              grid-row: 1 / 2;
              grid-column: 1 / 3; 
              // background-color: antiquewhite;
              display: flex;
              justify-content: center;
              align-items: center;

              color: rgb(215, 228, 255);
              font-family: cursive;
              font-size: xx-large;
          }
          
          &--1{
              grid-row: 2 / 3;
              grid-column: 1 / 2;
          }
          &--2{
              grid-row: 2 / 3;
              grid-column: 2 / 3;
          }
          //--------------------------------

          &--3{
              grid-row: 3 / 4;
              grid-column: 1 / 2;
          }
          &--4{
              grid-row: 3 / 4;
              grid-column: 2 / 3;
          }
          //--------------------------------

          &--5{
              grid-row: 4 / 5;
              grid-column: 1 / 2;
          }
          &--6{
              grid-row: 4 / 5;
              grid-column: 2 / 3;
          }
          //--------------------------------

          &--7{
            grid-row: 5 / 6;
            grid-column: 1 / 2;
          }
          &--8{
            grid-row: 5 / 6;
            grid-column: 2 / 3;
          }
          //--------------------------------

          &--9{
            grid-row: 6 / 7;
            grid-column: 1 / 2;
          }
          &--10{
            grid-row: 6 / 7;
            grid-column: 2 / 3;
          }
          //--------------------------------

          &--11{
            grid-row: 7 / 8;
            grid-column: 1 / 2;
          }
          &--12{
            grid-row: 7 / 8;
            grid-column: 2 / 3;
          }
          //--------------------------------
         
      }
      

      
      
  
      &__img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          display: block;
          cursor: pointer;
          box-shadow: 0 1.5rem 4rem rgba(black, 1.15);
  
          &:hover {
              -webkit-filter: brightness(50%);
              -webkit-transition: all 1s ease;
              -moz-transition: all 1s ease;
              -o-transition: all 1s ease;
              -ms-transition: all 1s ease;
              transition: all 0.5s ease;
          }
      }
  
  }
}



