.card {
perspective: 150rem;
-moz-perspective: 150rem;
position: relative;
height: 100%;

&__side {
  background-color: rgb(25, 55, 187);
  color: aliceblue;
  font-size: large;
  position: absolute;
  height: 100%;
  transition: all .8s;
  width: 100%;
  backface-visibility: hidden;
  top: 0;
  left: 0;

  &--front{
    background-color: rgb(32, 70, 177);
}

  %backprops{
    
  }

  &--back-1 {
    background-image: linear-gradient(80deg, rgb(255, 255, 255), rgba(156, 156, 156, 0.685));
    box-shadow: 0 1.5rem 4rem rgba(black, 1.15);
    transform: rotateY(180deg);
    grid: flex;
    flex-direction: column;
    align-items: center;
    }   
  &--back-2 {
    background-image: linear-gradient(to right bottom, rgb(184, 82, 82), rgba(27, 20, 95, 0.685));
    box-shadow: 0 1.5rem 4rem rgba(black, 1.15);
    transform: rotateY(180deg);
    grid: flex;
    flex-direction: column;
    align-items: center;
    }
  &--back-3 {
    background-image: linear-gradient(to right left, rgb(24, 89, 175), rgba(27, 20, 95, 0.685));
    box-shadow: 0 1.5rem 4rem rgba(black, 1.15);
    transform: rotateY(180deg);
    grid: flex;
    flex-direction: column;
    align-items: center;
    }



}

&:hover &__side--front{
  transform: rotateY(-180deg);
}

&:hover &__side--back-1 {
  transform: rotateY(0);
}
&:hover &__side--back-2 {
  transform: rotateY(0);
}
&:hover &__side--back-3 {
  transform: rotateY(0);
}
}





