footer {
	width: 100%;
	height: 15rem;
	background-color: rgb(29, 29, 29);
	color: azure;
	
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: row;
	gap: 15rem;
}
.footer-logo {
	color: #bb8787;
	font-size: 3rem;
}
.footer-logo span {
	font-size: 3rem;
    color: rgb(199, 223, 255);
}

.footer-text{
	font-size: 1.4rem;
}





@media screen and (max-width:900px) {
	footer {
		gap: 1rem;
	}
	.footer-logo {
		color: #bb8787;
		font-size: 2rem;
	}
	.footer-logo span {
		font-size: 2rem;
		color: rgb(199, 223, 255);
	}
	
	.footer-text{
		font-size: 1rem;
	}
}
@media screen and (max-width:400px) {
	footer {
		flex-direction: column;
		gap: 2rem;
	}
	.footer-logo {
		color: #bb8787;
		font-size: 1.5rem;
	}
	.footer-logo span {
		font-size: 1.5rem;
		color: rgb(199, 223, 255);
	}
	
	.footer-text{
		font-size: 1rem;
	}
}