.navbar {
    width: 100%;
    height: 7rem;
    top: 0;
    left: 0;
    background-image: linear-gradient(rgb(41, 41, 207),rgb(43, 92, 167));
    /* border-bottom: 1px solid #eee; */
}

.navbar .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
    height: 100%;
}

.logo {
    font-size: 1.5rem;
    font-weight: 400;
    color: #bb8787;
}

.navbar span {
    color: rgb(199, 223, 255);
}

.navbar .nav-menu {
    display: flex;
}

.navbar li {
    padding: 1rem;
    list-style-type: none;
    font-size: large;
}

.navbar a {
    text-decoration: none;
    color: #eee;
    font-size: ;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    transition: 0.2s;
}

.navbar a:hover {
    color: #8797bb;
}

.btn-container {
    display: flex;
}

.btn-signin {
    background-image: linear-gradient(180deg,rgba(53, 204, 171, 0.747),rgb(7, 105, 84));
}
.btn-register {
    background-image: linear-gradient(180deg,rgba(204, 53, 53, 0.747),rgb(105, 7, 7));
    margin-left: 0.5rem;
}

.hamburger {
    display: none;
}

.icon {
    color: azure;
    font-size: 1.5rem;
}

@media screen and (max-width:940px) {
    .hamburger {
        display: block;
    }

    .navbar .nav-menu {
        position: absolute;
        display: flex;
        flex-direction: column;
        align-items: center;
        top: 120px;
        /* left: 62rem; */
        background-color: #313131ad;
        right: 100%;
        height: 50%;
        width: 100%;
        z-index: 10;
        transition: 0.4s ease-in-out;
    }

    .navbar .active {
        right: 0;
        /* left: 0; */
    }

    .navbar .nav-menu li a {
        font-size: 1.5rem;
    }

}