.Section1 {

    background-image:linear-gradient(rgba(0, 0, 0, 0.336),rgba(0, 0, 0, 0.37)) ,url(../../../Assets/030.jpg);
    background-size: cover;
    background-position: top;
    position: relative;
    height: 55rem;
    clip-path: polygon(0 0, 100% 0, 100% 74%, 0 100%);
    z-index: 1;

    display: flex;
    /* align-items: center; */
    justify-content: center;
}

.Section1__Card {
    height: 30rem;
    width: 60rem;
    background-color: rgba(62, 88, 88, 0.562);
    margin-top: 6rem;
    padding: 10rem 0 13rem;
    border-radius: 1rem;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3rem;
    
    transition: all 0.8s;
}

.Section1__Card:hover {
    background-color: rgba(250, 235, 215, 0.288);
    cursor: pointer;
}

.Card__Votd {
    color: aliceblue;
    font-size: 3rem;
    text-decoration: underline;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

.Card__Chapter {
    color: rgb(181, 201, 255);
    font-size: 2.3rem;
    font-weight: 700;
    align-self: flex-start;
    margin-left: 5rem;
    margin-bottom: -1rem;
    font-family: Arial, Helvetica, sans-serif;
}
.Card__Verse {
    color: rgb(210, 222, 255);
    font-size: 2.3rem;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.main_title {
    color: rgb(169, 255, 255);
    font-size: 2rem;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    position: relative;
}


.hidden2 {
    opacity: 0;
    transition: all 1s;
    transform: translateX(-100%);
    filter: blur(5px);
  }
  
.show {
opacity: 1;
transform: translateX(0);
filter: blur(0);
}

  



@media screen and (max-width:1600px) {
    .Section1__Card {
        height: 30rem;
        width: 60rem;
        padding: 1rem 5rem;
    }
    .Card__Votd {
        margin-top: -2rem;
        font-size: 2.5rem;
    }
    .Card__Chapter {
        font-size: 1.8rem;
    }
    .Card__Verse {
        font-size: 1.6rem;
    }
}


@media screen and (max-width:940px) {
    .Section1__Card {
        height: 20rem;
        width: 40rem;
    }
    .Card__Votd {
        margin-top: -2rem;
        font-size: 2rem;
    }
    .Card__Chapter {
        font-size: 1.6rem;
    }
    .Card__Verse {
        font-size: 1.2rem;
    }


}


@media screen and (max-width:650px) {
    .Section1__Card {
        height: 20rem;
        width: 30rem;
    }
    .Card__Votd {
        /* margin-top: 2rem; */
    }
    .Card__Chapter {
        font-size: 1rem;
        margin-left: -1rem;
    }
    .Card__Verse {
        font-size: 1rem;
        padding-bottom: -1rem;
    }
}

