.container__signin {
    /* background-color: aquamarine; */
    background-color: rgb(39, 39, 39);
    width: 100%;
    height: 80vh;

    display: flex;
    justify-content: center;
}

.signin__container__form {
    margin-top: 5rem;
    background-image: linear-gradient(120deg,rgba(30, 109, 255, 0.671),rgba(76, 101, 129, 0.692));
    height: 20rem;
    width: 50rem;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: #eeeeee;

}


label{
    font-size: 1rem;
    color:azure;
    /* margin-left: -5rem; */
}

input{
    width: 30rem;
    height: 2rem;
    color: #363636;
    font-size: 0.8rem;
    
    border-radius: 0.5rem;

    border: 2px solid #dadada;
    border-radius: 7px;

}


input:focus { 
    outline:none;
    border-color:#9ecaed;
    box-shadow:0 0 10px #9ecaed;
}



.username {
    margin-top: 1rem;
    display: flex;
    flex-direction: column;
} 

.password {
    margin-top: 1rem;

    
    display: flex;
    flex-direction: column;
}

.signin-btn{
    margin-top: 2rem;
    
    height: 3rem;
    width: 6rem;
    font-size: 1rem; 
}



