.gallery {
    
    background-image: linear-gradient(
        to right bottom,
        rgba(rgb(37, 88, 199), 0.8),
        rgba(black, 0.8)
        ),
        url(../../../Assets/back.jpg);
        background-size: cover;
        background-position: top;
        position: relative;
        // clip-path: polygon(0 16%, 100% 0, 100% 100%, 0 100%);
        margin-top: -20rem;

        display: flex;
        align-items: center;
        justify-content: center;

        .gallery1 {
        // margin-top: 6rem;
        display: grid;
    
        grid-template-columns: repeat(2, 40rem);
        grid-template-rows: 20vh repeat(3, 25rem);
        
        grid-gap: 10rem;
        padding: 8rem;

        margin-top: 8rem;
        

        &__item {
            &--0{
                grid-row: 1 / 2;
                grid-column: 1 / 3; 
                // background-color: antiquewhite;
                display: flex;
                justify-content: center;
                align-items: last baseline;

                color: rgb(215, 228, 255);
                font-family: 'Arial Narrow Bold', sans-serif;
                font-size: xx-large;
            }
            
            &--1{
                grid-row: 2 / 3;
                grid-column: 1 / 2;
            }
            &--2{
                grid-row: 2 / 3;
                grid-column: 2 / 3;
            }
            //--------------------------------

            &--3{
                grid-row: 3 / 4;
                grid-column: 1 / 2;
            }
            &--4{
                grid-row: 3 / 4;
                grid-column: 2 / 3;
            }
            //--------------------------------

            &--5{
                grid-row: 4 / 5;
                grid-column: 1 / 2;
            }
            &--6{
                grid-row: 4 / 5;
                grid-column: 2 / 3;
            }
            //--------------------------------
           
        }
        

        
        
    
        &__img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            display: block;
            cursor: pointer;
            box-shadow: 0 1.5rem 4rem rgba(black, 1.15);
    
            &:hover {
                -webkit-filter: brightness(50%);
                -webkit-transition: all 1s ease;
                -moz-transition: all 1s ease;
                -o-transition: all 1s ease;
                -ms-transition: all 1s ease;
                transition: all 0.5s ease;
            }
        }
    
    }
}



.card {
  perspective: 150rem;
  -moz-perspective: 150rem;
  position: relative;
  height: 100%;

  &__side {
    background-color: rgb(25, 55, 187);
    color: aliceblue;
    font-size: large;
    position: absolute;
    height: 100%;
    transition: all .8s;
    width: 100%;
    backface-visibility: hidden;
    top: 0;
    left: 0;

    &--front{
      background-color: rgb(32, 70, 177);
  }

    %backprops{
      
    }

    &--back-1 {
      // background-image: linear-gradient(80deg, rgb(255, 255, 255), rgba(156, 156, 156, 0.685));
      box-shadow: 0 1.5rem 4rem rgba(black, 1.15);
      transform: rotateY(180deg);
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      }
    &--back-2 {
      background-image: linear-gradient(to right bottom, rgb(184, 82, 82), rgba(27, 20, 95, 0.685));
      box-shadow: 0 1.5rem 4rem rgba(black, 1.15);
      transform: rotateY(180deg);
      grid: flex;
      flex-direction: column;
      align-items: center;
      }
    &--back-3 {
      background-image: linear-gradient(to right left, rgb(24, 89, 175), rgba(27, 20, 95, 0.685));
      box-shadow: 0 1.5rem 4rem rgba(black, 1.15);
      transform: rotateY(180deg);
      grid: flex;
      flex-direction: column;
      align-items: center;
      }
  

  
  }

  &:hover &__side--front{
    transform: rotateY(-180deg);
  }
  
  &:hover &__side--back-1 {
    transform: rotateY(0);
  }
  &:hover &__side--back-2 {
    transform: rotateY(0);
  }
  &:hover &__side--back-3 {
    transform: rotateY(0);
  }
}




.back_btn {
  display: flex;
  justify-content: center;
  align-items: center;

  background-color: azure;
  height: 4rem;
  width: 12rem;
  border-radius: 1rem;
  border: 5px solid rgba(0, 0, 0, 0.658);
  text-decoration: none;
  font-weight: 500;
  color: rgb(0, 27, 102);
  padding: 0.3rem;
  cursor: pointer;
}

.back_btn:hover {
  background-color: rgba(240, 255, 255, 0.801);
}
.back_btn:active {
  background-color: rgb(212, 212, 212);
  border: 2px solid rgba(0, 0, 0, 0.658);  
}


.hidden {
  opacity: 0;
  transition: all 1s;
  transform: translateY(-100%);
  filter: blur(5px);
}

.show {
  opacity: 1;
  transform: translateX(0);
  filter: blur(0);
}



