body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: rgb(29, 29, 29); */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.btn {
  color: azure;
  font-size: 0.9rem;
  background-color: rgb(23, 62, 146);
  width: 4rem;
  height: 1.8rem;
  border-radius: 5px;
  text-decoration: none;
  cursor: pointer;
  /* text-align: center; */
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn:hover {
  background-color: brown;
  color: azure;
}

.btn:active {
  background-color: black;
  color: azure;
}



.heading-1 {
  font-weight: 500;
  font-size: 1.2rem;
  text-decoration: none;
}

.heading-2 {
  font-size: 1.3rem;
  color: rgb(255, 188, 162);
  text-decoration: none;
}
.heading-3 {
  font-weight: 500;
  font-size: 2.5rem;
  color: rgb(195, 255, 255);
}
.heading-4 {
  font-weight: 500;
  font-size: 4.2rem;
}


@media screen and (max-width:900px){
  .heading-4 {
    font-size: 3rem;
  }
}